// import { MatDialog } from "@angular/material/dialog";

import { Router, ActivatedRoute } from "@angular/router";
import { GeneralService } from "./general.service";
import { Injectable } from "@angular/core";
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse,
  HttpParams,
} from "@angular/common/http";
import { Observable } from "rxjs";
import { map, catchError, tap, flatMap } from "rxjs/operators";
import { AuthService } from ".";
import { appRoutes } from "../config";
import { ToastrService } from "ngx-toastr";

@Injectable()
export class BasicAuthInterceptor implements HttpInterceptor {
  constructor(private authService: AuthService) {}

  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    // add authorization header with basic auth credentials if available
    // debugger;
    if (request.method.toLowerCase() === "get") {
      const currentUserToken = this.authService.getUserToken();
      if (currentUserToken) {
        request = request.clone({
          url: `${request.url}${
            request.url.includes("?") ? "&" : "?"
          }token=${currentUserToken}&user_id=${
            this.authService.getUserLocally().user_id
          }`,
        });
      }
    }

    return next.handle(request);
  }
}

@Injectable()
export class InvalidAuthInterceptor implements HttpInterceptor {
  hasTriggered = false;
  constructor(
    private authService: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private route: ActivatedRoute
  ) {}

  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(
      catchError((err: any, caught) => {
        if (err instanceof HttpErrorResponse) {
          if (err.status === 401) {
            if (!this.hasTriggered) {
              // this.dialog.closeAll();
              this.authService.logout();
              this.router.navigate([appRoutes.login]);
              this.hasTriggered = true;
              this.toastr.success("You are logged out");
            }
          } else return next.handle(request);
        }
      })
    );
  }
}

@Injectable()
export class XMLtoJSONInterceptor implements HttpInterceptor {
  constructor(private generalService: GeneralService) {}

  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(
      map((event) => {
        if (event instanceof HttpResponse) {
          event = event.clone({
            body: this.generalService.xmlToJson(event.body),
          });
        }
        return event;
      })
    );
  }
}
