import { BrowserModule } from "@angular/platform-browser";
import { NgModule } from "@angular/core";

import { AppRoutingModule, routedGuards } from "./app-routing.module";
import { AppComponent } from "./app.component";
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { ToastrModule } from "ngx-toastr";
import {
  XMLtoJSONInterceptor,
  BasicAuthInterceptor,
  InvalidAuthInterceptor,
} from "./@core/interceptor.service";
import { MatRadioModule } from "@angular/material/radio";
import { JoyrideModule } from "ngx-joyride";

@NgModule({
  declarations: [AppComponent],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    HttpClientModule,
    MatRadioModule,
    ToastrModule.forRoot(),
    JoyrideModule.forRoot(),
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: XMLtoJSONInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: BasicAuthInterceptor, multi: true },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: InvalidAuthInterceptor,
      multi: true,
    },
    ...routedGuards,
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
