import { Injectable } from "@angular/core";
import { environment } from "../../environments/environment";
import { Observable, BehaviorSubject } from "rxjs";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { User } from "../@model/user";
import { GeneralService } from "./general.service";

@Injectable({ providedIn: "root" })
export class AuthService {
  private _presentStateSource = new BehaviorSubject<any>(false);
  public state$ = this._presentStateSource.asObservable() as Observable<any>;

  constructor(
    private httpClient: HttpClient,
    private generalService: GeneralService
  ) {}

  showLoader(loadingState) {
    this._presentStateSource.next(loadingState);
  }

  /**
   *  description: logs the user into the platform
   */
  login(user: any): Observable<any> {
    return this.httpClient.post(
      `${environment.baseURL}/member-login`,
      this.generalService.objectToFormData(user),
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   *  description: Registers a user and sends a verification email in the format
   */
  signup(user: any): Observable<any> {
    return this.httpClient.post(
      `${environment.baseURL}/member-register`,
      this.generalService.objectToFormData(user),
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   *  description: initailizes the process to reset password,
   * sends a verification email in the format {base_url}/#/change_password/{adverisers_email}/{generated_code}
   */
  forgotPassword(email: string): Observable<any> {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/forgot-password-request${this.generalService.generateSearchQuery({
        email,
      })}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   *  description: resets the users password
   */
  resetPassword(forgotObj: {
    pwd: string;
    email: string;
    code: string;
  }): Observable<any> {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/forgot-password-reset${this.generalService.generateSearchQuery(
        forgotObj
      )}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   *  description: Verifies the users Email
   */
  verifyEmail(user: any): Observable<any> {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/verify-token-request${this.generalService.generateSearchQuery(user)}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   *  description: Verifies the users Email
   */
  verifyRegistrationEmail(user: any): Observable<any> {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/email-verification${this.generalService.generateSearchQuery(user)}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   *   Resends Verification Code to Users
   */
  resendVerification(email: string) {
    return this.httpClient.get(
      `${
        environment.baseURL
      }/resend-email-verification${this.generalService.generateSearchQuery({
        email,
      })}`,
      <Object>{ responseType: "text/xml" }
    ) as Observable<any>;
  }

  /**
   * Changes the password type of the password field
   * it has just 2 states either "Hide" or "Show"
   * @param shouldShowPassword helps to know when to hide or show password
   * @param $event the triggered event
   * @param index presents the index of the input[type='password'] element in the parent
   * @returns {boolean}
   */
  changePasswordState(shouldShowPassword: boolean, event, index = 1): boolean {
    if (!shouldShowPassword) {
      event.target.parentElement.children[index].type = "password";
      return !shouldShowPassword;
    }
    event.target.parentElement.children[index].type = "text";
    return !shouldShowPassword;
  }

  logout() {
    this.httpClient
      .get(`${environment.baseURL}/member-logout`, <Object>{
        responseType: "text/xml",
      })
      .subscribe();
    localStorage.removeItem("__nv2_t");
    localStorage.removeItem("__nv2_");
    localStorage.removeItem("__onboard");
  }

  saveUserLocally(user): any {
    localStorage.setItem("__nv2_", JSON.stringify(user));
  }

  getUserLocally(): any {
    return JSON.parse(localStorage.getItem("__nv2_"));
  }

  saveUserToken(token: string): any {
    localStorage.setItem("__nv2_t", token);
  }

  getUserToken(): any {
    return localStorage.getItem("__nv2_t");
  }
}
